  src: url("/public/fonts/Futura_PT_Light/FTN35__W.woff") format("woff"), url("/public/fonts/Futura_PT_Light/FTN35__W.ttf") format("truetype"), url("/public/fonts/Futura_PT_Light/FTN35__W.svg") format("svg");
