@-webkit-keyframes animation-slit-in-vertical {
0% {
    -webkit-transform: translateZ(-800px) rotateY(90deg);
    transform: translateZ(-800px) rotateY(90deg);
    opacity: 0;
}
54% {
    -webkit-transform: translateZ(-160px) rotateY(87deg);
    transform: translateZ(-160px) rotateY(87deg);
    opacity: 1;
}
100% {
    -webkit-transform: translateZ(0) rotateY(0);
    transform: translateZ(0) rotateY(0);
}
}
@keyframes animation-slit-in-vertical {
0% {
    -webkit-transform: translateZ(-800px) rotateY(90deg);
    transform: translateZ(-800px) rotateY(90deg);
    opacity: 0;
}
54% {
    -webkit-transform: translateZ(-160px) rotateY(87deg);
    transform: translateZ(-160px) rotateY(87deg);
    opacity: 1;
}
100% {
    -webkit-transform: translateZ(0) rotateY(0);
    transform: translateZ(0) rotateY(0);
}
}
.form {
  position: relative;
  -webkit-transition: border-color linear 0.2s;
  transition: border-color linear 0.2s;
}
.form.form--success {
    border-color: green;
}
.form.form--failure {
    border-color: red;
}
.form .form-control,
  .form .custom-select {
    height: auto;
    padding: 10px 15px;
}
.form .custom-control-input:checked ~ .custom-control-label:before {
    border-color: #c9b486;
    background-color: #c9b486;
}
.form .custom-control-input:focus ~ .custom-control-label:before {
    -webkit-box-shadow: 0 0 0 0.2rem #c9b486;
            box-shadow: 0 0 0 0.2rem #c9b486;
}
.form .form-control:focus {
    border-color: #c9b486;
    -webkit-box-shadow: 0 0 0 0.2rem #c9b486;
            box-shadow: 0 0 0 0.2rem #c9b486;
}
.form__modal {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  background: rgba(255, 255, 255, 0.9);
}
.form__modal--success {
    -webkit-animation: animation-slit-in-vertical 0.45s ease-out both;
            animation: animation-slit-in-vertical 0.45s ease-out both;
}
.form__modal--failure {
    -webkit-animation: animation-slit-in-vertical 0.45s ease-out both;
            animation: animation-slit-in-vertical 0.45s ease-out both;
}
.form__submit {
  height: 50px;
  padding: 0 20px;
  min-width: 180px;
  border: none;
  border-radius: 2px;
  background: #c9b486;
  color: white;
  line-height: 50px;
}
.form__label {
  margin-bottom: 0;
}
.form__headline {
  margin: 0px 0 20px;
  font: 2rem/2rem "RistrettoPro-Light";
  text-align: center;
}
.form__subline {
  margin: 0px 0 20px;
  font: 1.5rem/1.5rem "RistrettoPro-Light";
  text-align: center;
}
