.module-contact-info {
  margin-bottom: 50px;
}
.module-contact-info__wrapper {
  padding: 30px;
  background: #f2f2f2;
}
.module-contact-info__wrapper .nav-tabs {
    margin-bottom: 30px;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    border: none;
    font: 2rem/1 "RistrettoPro-Light";
}
.module-contact-info__wrapper .nav-link {
    color: black;
    border: none;
}
.module-contact-info__wrapper .nav-link.active {
      background: transparent;
      text-decoration: underline;
}
.module-contact-info__wrapper .tab-pane {
    outline: none;
}
.module-contact-info__wrapper .promo {
    margin: 20px auto;
    width: 330px;
}
.module-contact-info__headline {
  margin: 20px 0;
  font: 3rem/3rem "RistrettoPro-Light";
  text-align: center;
}
