@-webkit-keyframes animation-fade-in-sidebar {
from {
    -webkit-transform: translate(3rem, 0);
            transform: translate(3rem, 0);
    opacity: 0;
}
to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    opacity: 1;
}
}
@keyframes animation-fade-in-sidebar {
from {
    -webkit-transform: translate(3rem, 0);
            transform: translate(3rem, 0);
    opacity: 0;
}
to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    opacity: 1;
}
}
.module-dealers-map__headline {
  margin: 30px 0;
  font: 3rem/3rem "RistrettoPro-Light";
  text-align: center;
}
.module-dealers-map__list {
  margin: 20px 0;
  padding: 13px 80px;
  background: #e6e6e6;
}
.module-dealers-map__list .custom-select {
    height: 50px;
    border: none;
}
.module-dealers-map__wrapper {
  position: relative;
  margin: 35px 0;
  padding-top: 50%;
  overflow: hidden;
}
.module-dealers-map__map {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
}
.module-dealers-map__sidebar {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: absolute;
  right: 0;
  top: 0;
  width: 400px;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 40px;
  background: white;
}
.module-dealers-map__sidebar--show {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-animation: animation-fade-in-sidebar 0.2s cubic-bezier(0.55, 0, 0.1, 1);
            animation: animation-fade-in-sidebar 0.2s cubic-bezier(0.55, 0, 0.1, 1);
}
.module-dealers-map__sidebar--show > * {
      display: block;
      -webkit-animation: animation-fade-in-sidebar 1s cubic-bezier(0.55, 0, 0.1, 1);
              animation: animation-fade-in-sidebar 1s cubic-bezier(0.55, 0, 0.1, 1);
}
.module-dealers-map__sidebar--show > *:nth-child(1) {
        -webkit-animation-duration: 0.2s;
                animation-duration: 0.2s;
}
.module-dealers-map__sidebar--show > *:nth-child(2) {
        -webkit-animation-duration: 0.4s;
                animation-duration: 0.4s;
}
.module-dealers-map__sidebar--show > *:nth-child(3) {
        -webkit-animation-duration: 0.6s;
                animation-duration: 0.6s;
}
.module-dealers-map__sidebar--show > *:nth-child(4) {
        -webkit-animation-duration: 0.8s;
                animation-duration: 0.8s;
}
.module-dealers-map__sidebar--hide {
    display: none;
}
.module-dealers-map__sidebar--hide > * {
      display: none;
}
.module-dealers-map__sidebar-close {
  position: absolute;
  padding: 0 5px;
  right: 5px;
  top: 10px;
  cursor: pointer;
  border: none;
  background: white;
}
.module-dealers-map__sidebar-headline {
  margin: 0px 0 30px;
  font: 2rem/1 "RistrettoPro-Medium";
}
.module-dealers-map__sidebar-subline {
  margin: 0px 0 10px;
  font: 1.2rem/1 "RistrettoPro-Light";
}
.module-dealers-map__sidebar-copy {
  margin: 0px 0 30px;
  font-size: 1rem;
}
.module-dealers-map__sidebar-list {
  padding: 0;
}
.module-dealers-map__sidebar-list-item {
  display: inline-block;
}
.module-dealers-map__sidebar-icon {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin-right: 10px;
  width: 30px;
  height: 30px;
  border-radius: 30px;
  background: #c9b486;
  border: 1px solid white;
  text-align: center;
  color: white;
}
.module-dealers-map__sidebar-icon:hover {
    color: #c9b486;
    background: white;
    border-color: #c9b486;
}
