.icon-toggle {
  position: relative;
  margin: auto;
}
.icon-toggle .inner {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
}
.icon-toggle.hamburger .inner {
    top: 50%;
    display: block;
    margin-top: -2px;
    -webkit-transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
            transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
    -webkit-transition-duration: 75ms;
            transition-duration: 75ms;
}
.icon-toggle.hamburger .inner, .icon-toggle.hamburger .inner:after, .icon-toggle.hamburger .inner:before {
      position: absolute;
      width: 20px;
      height: 2px;
      -webkit-transition-timing-function: ease;
              transition-timing-function: ease;
      -webkit-transition-duration: .15s;
              transition-duration: .15s;
      -webkit-transition-property: -webkit-transform;
      transition-property: -webkit-transform;
      transition-property: transform;
      transition-property: transform, -webkit-transform;
      border-radius: 2px;
      background-color: #000;
}
.icon-toggle.hamburger .inner:after, .icon-toggle.hamburger .inner:before {
      display: block;
      content: "";
}
.icon-toggle.hamburger .inner:before {
      top: -7px;
      -webkit-transition: top 75ms ease .12s,opacity 75ms ease;
      transition: top 75ms ease .12s,opacity 75ms ease;
}
.icon-toggle.hamburger .inner:after {
      bottom: -7px;
      -webkit-transition: bottom 75ms ease 0.12s, -webkit-transform 75ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
      transition: bottom 75ms ease 0.12s, -webkit-transform 75ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
      transition: bottom 75ms ease 0.12s, transform 75ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
      transition: bottom 75ms ease 0.12s, transform 75ms cubic-bezier(0.55, 0.055, 0.675, 0.19), -webkit-transform 75ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
.icon-toggle.hamburger.is-active .inner {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-transition-delay: .12s;
            transition-delay: .12s;
    -webkit-transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
            transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}
.icon-toggle.hamburger.is-active .inner:before {
      top: 0;
      opacity: 0;
      -webkit-transition: top 75ms ease,opacity 75ms ease .12s;
      transition: top 75ms ease,opacity 75ms ease .12s;
}
.icon-toggle.hamburger.is-active .inner:after {
      bottom: 0;
      -webkit-transform: rotate(-90deg);
              transform: rotate(-90deg);
      -webkit-transition: bottom 75ms ease, -webkit-transform 75ms cubic-bezier(0.215, 0.61, 0.355, 1) 0.12s;
      transition: bottom 75ms ease, -webkit-transform 75ms cubic-bezier(0.215, 0.61, 0.355, 1) 0.12s;
      transition: bottom 75ms ease, transform 75ms cubic-bezier(0.215, 0.61, 0.355, 1) 0.12s;
      transition: bottom 75ms ease, transform 75ms cubic-bezier(0.215, 0.61, 0.355, 1) 0.12s, -webkit-transform 75ms cubic-bezier(0.215, 0.61, 0.355, 1) 0.12s;
}
