.font-peignot {
  font-family: "Peignot Light", Helvetica, Arial, sans-serif !important;
  font-size: 1.1em;
  display: inline-block;
  text-transform: uppercase;
  vertical-align: initial;
  -webkit-transform: scaleY(0.9);
  transform: scaleY(0.9);
}

