.promo-contact-card {
  padding: 20px;
  background: white;
  max-width: 400px;
}
@media screen and (max-width: 767px) {
.promo-contact-card {
      max-width: 100%;
}
}
.promo-contact-card__description {
  margin: 15px 0;
  text-align: center;
}
.promo-contact-card__headline {
  margin: 20px 0;
  text-align: center;
  font: 1.5rem/1 "RistrettoPro-Light";
  color: black;
  letter-spacing: 2px;
}
.promo-contact-card__subline {
  margin: 15px 0;
  text-align: center;
  font: 0.8rem/1 "Futura PT Light";
  text-transform: uppercase;
  color: #a2a2a2;
  letter-spacing: 2px;
}
.promo-contact-card__contact {
  margin: 10px 0;
  text-align: center;
  font: 1rem/1 "Futura PT Light";
  color: black;
  letter-spacing: 3px;
}
.promo-contact-card__button {
  margin-top: 30px;
}
