.promo-coupon {
  height: 100%;
  max-width: 600px;
  max-height: 600px;
}
.promo-coupon__image {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 500;
}
.promo-coupon__card {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: absolute;
  padding: 20px;
  margin: auto;
  height: 100%;
  width: 50%;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.8);
  z-index: 502;
}
.promo-coupon__headline {
  width: 100%;
  margin: 0px 0 20px;
  font: 1.5rem/2rem "RistrettoPro-Light";
  text-align: center;
}
.promo-coupon__subline {
  width: 100%;
  margin: 0px 0 20px;
  font: 1.2rem/1.5rem "RistrettoPro-Light";
  text-align: center;
  letter-spacing: 2px;
}
.promo-coupon__copy {
  width: 100%;
  font-size: 0.8rem;
  text-align: center;
}
