.promo-feedback {
  width: 300px;
  overflow: hidden;
}
.promo-feedback__wrapper {
  position: relative;
  -webkit-transition: -webkit-transform linear 0.4s;
  transition: -webkit-transform linear 0.4s;
  transition: transform linear 0.4s;
  transition: transform linear 0.4s, -webkit-transform linear 0.4s;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.promo-feedback__wrapper:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
}
.promo-feedback__wrapper:hover .promo-feedback__content-wrapper {
      opacity: 1;
}
.promo-feedback__content-wrapper {
  position: absolute;
  margin: 5%;
  height: 90%;
  width: 90%;
  left: 0;
  top: 0;
  opacity: 0;
  background: rgba(255, 255, 255, 0.9);
  -webkit-transition: opacity linear 0.4s;
  transition: opacity linear 0.4s;
}
