.promo-label {
  padding: 20px;
  height: 400px;
  width: 500px;
  background: #e6e6e6;
  border: 10px solid #f2f2f2;
}
.promo-label__wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 20px;
  height: 100%;
  border: 1px solid #e6e6e6;
  background: white;
}
.promo-label_headline {
  width: 80%;
  margin: 10px 0;
  text-align: center;
  font: 4rem/1 "RistrettoPro-Medium";
  letter-spacing: 0px;
  color: black;
}
.promo-label_copy {
  width: 80%;
  margin: 10px 0;
  text-align: center;
  font: 2rem/1 "RistrettoPro-Light";
  letter-spacing: 1px;
  color: #515151;
}
.promo-label_button {
  margin: 10px 0;
  background: black;
}
