@-webkit-keyframes animation-fade-in-popup {
from {
    -webkit-transform: translate(0, -3rem);
            transform: translate(0, -3rem);
    opacity: 0;
}
to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    opacity: 1;
}
}
@keyframes animation-fade-in-popup {
from {
    -webkit-transform: translate(0, -3rem);
            transform: translate(0, -3rem);
    opacity: 0;
}
to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    opacity: 1;
}
}
.popup {
  position: relative;
  margin: auto;
  -webkit-animation: animation-fade-in-popup 0.3s normal;
          animation: animation-fade-in-popup 0.3s normal;
  -webkit-box-shadow: 0 0 28px 0 rgba(0, 0, 0, 0.75);
          box-shadow: 0 0 28px 0 rgba(0, 0, 0, 0.75);
}
.popup__recaptcha {
  position: relative;
  margin: 10px 0;
  width: 302px;
  overflow: hidden;
}
.popup__close {
  position: absolute;
  right: 1rem;
  top: 1rem;
  border: none;
  cursor: pointer;
  background: none;
  z-index: 1001;
  font-size: 30px;
  line-height: 30px;
}
.popup__close:focus {
    outline: none;
}
.popup__close:active {
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
}
@media screen and (max-width: 767px) {
.popup__close {
      top: 0.5rem;
      right: 0.5rem;
}
}
.form__input-text {
  width: 100%;
}
.form__divider {
  margin: 10px;
  height: 1px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
