.promo-topical {
  width: 300px;
}
.promo-topical__wrapper {
  position: relative;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
}
.promo-topical__image-wrapper {
  -webkit-filter: blur(5px);
          filter: blur(5px);
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: all 0.1s linear;
  transition: all 0.1s linear;
}
.promo-topical__wrapper:hover .promo-topical__image-wrapper {
    -webkit-filter: none;
            filter: none;
}
.promo-topical__content-wrapper {
  position: absolute;
  padding: 10px;
  bottom: 0;
  width: 100%;
  background: rgba(255, 255, 255, 0.8);
  -webkit-transition: all 0.1s linear;
  transition: all 0.1s linear;
}
.promo-topical__wrapper:hover .promo-topical__content-wrapper {
    background: white;
}
.promo-topical__headline {
  display: block;
  text-align: center;
}
.promo-topical__headline:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
}
