@-webkit-keyframes animation-show-image {
from {
    -webkit-transform: translateY(-3rem);
            transform: translateY(-3rem);
    opacity: 0;
}
to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
}
}
@keyframes animation-show-image {
from {
    -webkit-transform: translateY(-3rem);
            transform: translateY(-3rem);
    opacity: 0;
}
to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
}
}
.image-deferred {
  position: relative;
  overflow: hidden;
  margin: 0;
}
.image-deferred:before {
    content: '';
    display: block;
}
.image-deferred.fresco {
  cursor: -webkit-zoom-in;
  cursor: zoom-in;
}
.image-deferred--preloader {
  background-image: url("/public/images/watermark.png");
  background-size: 33%;
  background-position: 50%;
}
.image-deffered--size-1-1 {
  padding-top: 100%;
}
.image-deffered--size-1-2 {
  padding-top: 40%;
}
.image-deffered--size-2-3 {
  padding-top: 150%;
}
.image-deffered--size-3-2 {
  padding-top: 66.66667%;
}
.image-deferred__image,
.image-deferred img {
  position: absolute;
  margin: auto;
  width: 100%;
  bottom: 0;
  left: 0;
  top: 0;
}
.image--effect-fadein {
  -webkit-animation-name: animation-show-image;
          animation-name: animation-show-image;
  -webkit-animation-direction: normal;
          animation-direction: normal;
}
.image--effect-zoom-out img {
  -webkit-transition: -webkit-transform linear 0.2s;
  transition: -webkit-transform linear 0.2s;
  transition: transform linear 0.2s;
  transition: transform linear 0.2s, -webkit-transform linear 0.2s;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.image--effect-zoom-out:hover img {
  -webkit-transform: scale(1);
          transform: scale(1);
}
