.popup-subscribe {
  position: relative;
  width: 400px;
  padding: 0;
  border: 1px solid #e6e6e6;
  background: white;
  -webkit-box-shadow: 10px -10px 15px 10px white;
          box-shadow: 10px -10px 15px 10px white;
}
@media screen and (max-width: 767px) {
.popup-subscribe {
      width: 90%;
}
}
.popup-subscribe__form {
  padding: 20px;
  border: none;
}
