@-webkit-keyframes animation-fade-in-article {
from {
    -webkit-transform: translate(0, -3rem);
            transform: translate(0, -3rem);
    opacity: 0;
}
to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    opacity: 1;
}
}
@keyframes animation-fade-in-article {
from {
    -webkit-transform: translate(0, -3rem);
            transform: translate(0, -3rem);
    opacity: 0;
}
to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    opacity: 1;
}
}
.module-article {
  margin: 10px 0;
  padding: 15px;
  -webkit-animation: animation-fade-in-article 0.2s linear;
          animation: animation-fade-in-article 0.2s linear;
}
.module-article__wrapper {
  padding: 30px;
  background: white;
  -webkit-box-shadow: 0px 0px 10px 5px #e6e6e6;
          box-shadow: 0px 0px 10px 5px #e6e6e6;
}
@media screen and (max-width: 767px) {
.module-article__wrapper {
      padding: 20px;
}
}
.module-article__wrapper h1 {
    margin: 2rem 0;
    letter-spacing: 0;
    text-align: center;
    font: 4rem/1 "RistrettoPro-Light";
    color: black;
}
@media screen and (max-width: 767px) {
.module-article__wrapper h1 {
        margin: 1.5rem 0;
        text-align: center;
}
}
.module-article__wrapper h2 {
    margin: 2rem 0;
    letter-spacing: 0;
    text-align: center;
    font: 3rem/1 "RistrettoPro-Light";
    color: black;
}
@media screen and (max-width: 767px) {
.module-article__wrapper h2 {
        margin: 1.5rem 0;
        text-align: center;
}
}
.module-article__wrapper h3 {
    margin: 2rem 0;
    letter-spacing: 0;
    text-align: center;
    font: 2rem/1 "RistrettoPro-Light";
    color: black;
}
@media screen and (max-width: 767px) {
.module-article__wrapper h3 {
        margin: 1rem 0;
        text-align: center;
}
}
.module-article__wrapper img {
    width: 100%;
}
.module-article__wrapper video {
    width: 100%;
}
.module-article__wrapper p {
    margin: 2rem 0;
    color: #515151;
}
@media screen and (max-width: 767px) {
.module-article__wrapper p {
        margin: 1rem 0;
}
}
.module-article__wrapper a {
    color: black;
    text-decoration: underline;
}
.module-article__isotope img {
  margin: 0.5%;
  width: 24%;
}
@media screen and (max-width: 1023px) {
.module-article__isotope img {
      margin: 0.5%;
      width: 32%;
}
}
@media screen and (max-width: 767px) {
.module-article__isotope img {
      margin: 1%;
      width: 48%;
}
}
