.module-dress-info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  overflow: hidden;
}
@media screen and (max-width: 767px) {
.module-dress-info {
      padding: 0;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
}
.module-dress-info__carousel {
  position: relative;
  width: 50%;
  overflow: hidden;
}
@media screen and (max-width: 767px) {
.module-dress-info__carousel {
      min-height: 600px;
      width: 100%;
}
}
.module-dress-info__carousel .flickity-viewport {
    height: 100% !important;
}
.module-dress-info__carousel .flickity-slider {
    height: 100% !important;
}
.module-dress-info__carousel .flickity-prev-next-button {
    position: absolute;
    margin: auto;
    bottom: 10%;
    width: 40px;
    height: 40px;
}
.module-dress-info__carousel .flickity-prev-next-button:disabled {
      opacity: 0.6;
}
.module-dress-info__carousel .flickity-prev-next-button.previous {
      left: 10%;
}
.module-dress-info__carousel .flickity-prev-next-button.next {
      right: 10%;
}
.module-dress-info__carousel .flickity-prev-next-button .flickity-button-icon {
      height: 60%;
      width: 60%;
      top: 0;
      bottom: 0;
      position: absolute;
      left: 0;
      right: 0;
      margin: auto;
}
.module-dress-info__carousel .image-deferred {
    padding-top: 0 !important;
}
@media screen and (max-width: 767px) {
.module-dress-info__carousel .image-deferred {
        padding-top: 150% !important;
}
}
.module-dress-info__image {
  width: 100%;
}
.module-dress-info__image .image-deferred {
    height: 100%;
}
.module-dress-info__image {
  height: 100%;
}
.module-dress-info__info {
  width: 50%;
  background: white;
}
@media screen and (max-width: 767px) {
.module-dress-info__info {
      width: 100%;
}
}
.module-dress-info__info-wrapper {
  padding: 2rem;
}
.module-dress-info__title {
  text-align: center;
  font: 3rem/1 "RistrettoPro-Light";
  color: black;
}
.module-dress-info__subline {
  margin: 0.5rem 0;
  letter-spacing: 1px;
  font: 1.5rem/1 "RistrettoPro-Light";
  color: #a2a2a2;
}
