.promo-brand {
  width: 250px;
}
.promo-brand:hover .promo-news__wrapper {
    -webkit-box-shadow: none;
            box-shadow: none;
}
.promo-brand__image-wrapper:after {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  opacity: 0.75;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, rgba(33, 33, 33, 0)), to(#212121));
  background-image: linear-gradient(-180deg, rgba(33, 33, 33, 0) 0, #212121 100%);
}
.promo-brand__headline {
  position: absolute;
  margin: auto;
  padding: 2rem;
  bottom: 0;
  right: 0;
  left: 0;
  top: 60%;
  z-index: 500;
  text-align: center;
  font: 2rem/1 "RistrettoPro-Light";
  color: white;
}
