@-webkit-keyframes move {
0% {
    background-position: 0 0;
}
100% {
    background-position: 28px 0;
}
}
@keyframes move {
0% {
    background-position: 0 0;
}
100% {
    background-position: 28px 0;
}
}
.module-subscribe {
  min-height: 410px;
  border: none;
  border-top: 1px solid #f2f2f2;
  background: #fafafa;
}
@media screen and (max-width: 1023px) {
.module-subscribe {
      display: none;
}
}
.module-subscribe__wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 100px 30px;
  background: none;
}
.module-subscribe__headline {
  margin-bottom: 30px;
  font: 4rem/1em 'Akzidenz Grotesk';
  letter-spacing: 5px;
  color: black;
}
.module-subscribe__subline {
  margin: 10px 20px 25px;
  font: 1rem/1em 'Akzidenz Grotesk';
  letter-spacing: 2px;
  color: black;
}
.module-subscribe__form {
  display: inline-block;
  position: relative;
  width: 70%;
}
.module-subscribe__form-input {
  display: block;
  position: relative;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 30px 0 10px;
  border: 1px solid black;
  font: 2rem/4rem 'Akzidenz Grotesk';
  letter-spacing: 2px;
  color: black;
}
.module-subscribe--request .module-subscribe__form-input {
    background-image: repeating-linear-gradient(-45deg, white, white 11px, #e6e6e6 10px, #e6e6e6 20px);
    background-size: 28px 28px;
    -webkit-animation: move .5s linear infinite;
            animation: move .5s linear infinite;
}
.module-subscribe__form-input:focus {
    outline: none;
}
.module-subscribe__form-input:disabled {
    opacity: 0.5;
}
.module-subscribe__form-submit {
  position: absolute;
  height: 100%;
  right: 0;
  top: 0;
  font-size: 2rem;
  padding: 0 20px;
  vertical-align: middle;
  background: #1c1d21 url("/public/images/icons/icon-arrow-right-white.svg") no-repeat center;
  border: 20px solid #1c1d22;
  text-indent: -9999px;
}
.module-subscribe__form-submit:hover {
    opacity: 0.8;
}
.module-subscribe__form-submit:focus {
    outline: none;
}
.module-subscribe__form-submit:disabled {
    opacity: 0.5;
}
